import { html } from 'lit';
import { ifDefined } from 'lit-html/directives/if-defined.js';

import { TabTemplate } from '../../market-tab/storybook/market-tab.templates';

export type TMarketTabListTemplateArgs = Partial<HTMLMarketTabListElement> & {
  disabledTabIndices?: string;
  numberOfTabs?: number;
  tabLabels?: string;
};

export const TabListTemplate = ({
  disabledTabIndices,
  numberOfTabs,
  selectedTab,
  size,
  tabLabels,
}: TMarketTabListTemplateArgs) => {
  const disabledTabIndicesList = (() => {
    try {
      return disabledTabIndices.split(',').map((i) => Number.parseInt(i, 10)) ?? [];
    } catch {
      return [];
    }
  })();
  const tabLabelsList = tabLabels?.split?.(',') ?? [];

  return html`
    <market-tab-list size=${ifDefined(size)} selected-tab=${ifDefined(selectedTab)}>
      ${Array.from({ length: numberOfTabs })
        .fill(undefined)
        .map((_, index) =>
          TabTemplate({
            ariaControls: `panel-${index + 1}`,
            disabled: disabledTabIndicesList.includes(index),
            id: `tab-${index + 1}`,
            text: tabLabelsList[index] ?? `Tab ${index + 1}`,
          }),
        )}
    </market-tab-list>
  `;
};
